.App {
  min-height: 100vh;
  background-image: linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%);
}
.search-form {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.search-bar {
  widows: 50%;
  border: none;
  padding: 10px;
}
.search-button {
  background: lightgreen;
  border: none;
  padding: 10px 20px;
  color: black;
}
.recipes {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
